/*##IMPORT AWESOME FONT ICONS##*/
/*Refer to http://fortawesome.github.com/Font-Awesome/#all-icons to check all icons */

.accordion {
  clear: both;
}

/*##VERTICAL ACCORDION##*/
.accordion-ver > ul,
.accordion-hor > ul {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
.accordion-ver > ul > li {
  display: block;
  margin: 0;
  list-style: none;
  clear: left;
  position: relative;
  width: 100%;
  border-bottom: #f0f0f0 1px solid;
  background: #cccccc;
  *margin-bottom: -4px; /* IE6 e IE7 */
}
.accordion-ver > ul > li:hover {
  cursor: pointer;
}
.accordion-ver > ul > li.last {
  border: 0;
}
.accordion-ver > ul > li > h1 {
  position: absolute;
  display: block;
  float: left;
  margin: 0;
  padding: 0 0 0 10px;
  top: 0;
  left: 0;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  font-size: 1.1em;
  font-weight: 500;
}
.accordion-ver > ul > li > h1:hover {
  cursor: pointer;
}
.accordion-ver > ul > li > div {
  display: none;
  background: #666;
  overflow: hidden;
}
.accordion-ver:hover > ul > li:hover > div {
  cursor: default;
}
.accordion-ver > ul > li > div > span {
  padding: 10px;
  display: block;
  text-align: left !important;
  line-height: normal;
}
.accordion-ver > ul > li > span.numericTab {
  position: absolute;
  display: block;
  top: 0;
  padding: 0 10px 0 0;
  float: right;
  margin: 0;
  right: 0;
  text-decoration: none;
  color: #000;
  background: #cccccc;
}
.accordion-ver > ul > li > i {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  padding: 0 10px 0 0;
  float: right;
  margin: 0;

  text-decoration: none;
  color: #000;
}

/*##HORIZONTAL ACCORDION##*/
.accordion-hor > ul > li {
  display: block;
  margin: 0;
  list-style: none;
  overflow: hidden;
  float: left;
  padding: 0;
  position: relative;
  border-right: #f0f0f0 1px solid;
  background: #566ba3;
  background-size: cover;
}

.accordion-hor > ul > li.active {
  background-color: #d87900;
}

.accordion-hor ul li p a.btn {
  background-color: #d87900 !important;
  background-image: none !important;
  font-size: 1em;
  position: absolute;
  top: 100px;
  left: 60px;
}

.accordion-hor > ul > li:hover {
  cursor: pointer;
}
.accordion-hor > ul > li.last {
  border: 0;
}
#content .accordion-hor > ul > li > h1 {
  position: absolute;
  display: block;
  float: left;
  /*margin-top: 0;*/
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  width: 100%;
  top: 30px;
  text-align: left;
  text-decoration: none;
  color: #fff;
  font-size: 1.1em;

  /* CSS3 Transform Rotate & Translate for H1 */
  white-space: nowrap;
  -moz-transform: rotate(90deg) translate(-40px, 0px); /* FF3.5+ */
  -moz-transform-origin: 0 100%;
  -o-transform: rotate(90deg) translate(-40px, 0px); /* Opera 10.5 */
  -o-transform-origin: 0 100%;
  -webkit-transform: rotate(90deg) translate(-40px, 0px); /* Saf3.1+, Chrome */
  -webkit-transform-origin: 0 100%;
  transform: rotate(90deg) translate(-40px, 0px); /* Saf3.1+, Chrome */
  transform-origin: 0 100%;
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=1.0); /* IE6,IE7 */
  -ms-filter: "progid:DXImageTransform.Microsoft.BasicImage(rotation=1.0)"; /* IE8 */

  background: rgba(0, 0, 0, 0.4);
  padding: 5px 10px;
  width: auto;
  margin-left: -5px;
}
.accordion-hor > ul > li > h1:hover {
  cursor: pointer;
}
.accordion-hor > ul > li > div {
  position: relative;
  display: block;
  float: left;
  overflow: hidden;
  *top: 0px; /* IE7 Hack */
  *left: 0px; /* IE7 Hack */
  margin: 0;
  background: #eee;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px;
  max-width: 85%;
  color: #fff;
}
.accordion-hor:hover > ul > li:hover > div {
  cursor: default;
}
.accordion-hor > ul > li > div > span {
  padding: 10px;
  display: block;
  text-align: left !important;
  line-height: normal;
}
.accordion-hor > ul > li > span.numericTab {
  position: absolute;
  display: block;
  bottom: 0;
  padding: 10px 0;
  float: left;
  margin: 0;
  text-decoration: none;
  text-align: center;
  color: #000;
  background: #cccccc;
}
.accordion-hor > ul > li > i {
  position: absolute;
  display: block;
  bottom: 0;
  padding: 10px 0;
  float: left;
  margin: 0;
  text-decoration: none;
  color: #000;
}
